/*Header css*/
.header-logo {
  /* animation: App-logo-spin infinite 20s linear;*/
  height: 60px;
  width: auto;
  margin-right: 20px;
  margin-left: 20px; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.header {
  /*z-index: 2;
    position:fixed;
    top:0;*/
  color: black;
  text-align: left;
  width: 100%;
  font-size: large;
  line-height: 50px;
  -webkit-box-shadow: 0px 10px 70px rgba(0, 0, 0, 0.075);
          box-shadow: 0px 10px 70px rgba(0, 0, 0, 0.075);
  display: -ms-flexbox;
  display: flex; }

.header * {
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-item-align: center;
      align-self: center; }

.header div {
  -ms-flex-wrap: wrap;
      flex-wrap: wrap; }

.header h2 {
  display: -ms-inline-flexbox;
  display: inline-flex;
  margin-right: 30px; }

.headerButton_Container {
  overflow: hidden;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap; }

.headerButton_Container * {
  margin-right: 20px; }

@media (max-width: 600px) {
  .header-logo {
    height: 100px; }
  .header {
    margin: auto;
    width: 100%;
    -webkit-box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.125);
            box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.125); }
  .headerButton_Container {
    width: 70%; }
  .headerButton_Container * {
    display: block; }
  .headerButton {
    width: 100%; } }

.App-intro {
  font-size: large; }

.headerButton, .homeButton {
  cursor: pointer; }

.headerButton {
  text-decoration: none;
  color: darkgray; }

.activeHeaderButton, .headerButton:hover {
  color: black;
  text-decoration: underline; }

/*Home Page*/
@-webkit-keyframes fadein {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }
@keyframes fadein {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

.Content_Container {
  margin-top: 5px;
  margin-bottom: 30px;
  /*padding-bottom: 150px;*/
  -webkit-animation: 2s linear 0s 1 fadein;
          animation: 2s linear 0s 1 fadein;
  clear: left; }

