  height: 100%;
  width: 100%;
  top: 0;
  right: 0; }

.videoOverlay:hover {
  /*background-color: rgba(40,150,256,.2);*/
  /*background-color: rgba(124,124,124,.3);*/
  /*background-color: rgba(50,100,256,.2) use this one*/ }

.homeButton {
  display: inline-block;
  /*background: linear-gradient(0deg,rgba(124,124,124,.3),rgba(124,124,124,.3)), url("resources/computer.jpg") no-repeat;*/
  color: white;
  float: left;
  position: absolute;
