  /*box-shadow: 20px 20px 20px rgba(120,120,120,.3);*/ }

.homeButton_Container {
  background-color: whitesmoke; }

#Photography, #Coding, #Design {
  float: left;
  position: absolute;
  overflow: hidden; }

/*Defaults */
#Photography, #bigPhotography, #skinnyPhotography {
  height: 100%;
  width: 49.6%;
  top: 0;
  right: 0;
  background: -webkit-gradient(linear, left bottom, left top, from(rgba(50, 100, 255, 0.3)), to(rgba(50, 100, 255, 0.1))), url("resources/cameraCover.jpg") no-repeat;
  background: -webkit-linear-gradient(bottom, rgba(50, 100, 255, 0.3), rgba(50, 100, 255, 0.1)), url("resources/cameraCover.jpg") no-repeat;
  background: -o-linear-gradient(bottom, rgba(50, 100, 255, 0.3), rgba(50, 100, 255, 0.1)), url("resources/cameraCover.jpg") no-repeat;
  background: linear-gradient(0deg, rgba(50, 100, 255, 0.3), rgba(50, 100, 255, 0.1)), url("resources/cameraCover.jpg") no-repeat; }

#Coding, #skinnyCode, #shortCode, #bigCoding {
  top: 0;
  left: 0;
  height: 49.25%;
  width: 49.6%;
  background-position: right;
  background: -webkit-gradient(linear, left bottom, left top, from(rgba(50, 100, 255, 0.3)), to(rgba(50, 100, 255, 0.1))), url("resources/codeImage.jpg") no-repeat;
  background: -webkit-linear-gradient(bottom, rgba(50, 100, 255, 0.3), rgba(50, 100, 255, 0.1)), url("resources/codeImage.jpg") no-repeat;
  background: -o-linear-gradient(bottom, rgba(50, 100, 255, 0.3), rgba(50, 100, 255, 0.1)), url("resources/codeImage.jpg") no-repeat;
  background: linear-gradient(0deg, rgba(50, 100, 255, 0.3), rgba(50, 100, 255, 0.1)), url("resources/codeImage.jpg") no-repeat; }

#Design, #skinnyDesign, #shortDesign, #bigDesign {
  bottom: 0;
  left: 0;
  height: 49.25%;
  width: 49.6%;
  background-position: right;
  background: -webkit-gradient(linear, left bottom, left top, from(rgba(50, 100, 255, 0.3)), to(rgba(50, 100, 255, 0.1))), url("resources/aboutCover.jpg") no-repeat;
  background: -webkit-linear-gradient(bottom, rgba(50, 100, 255, 0.3), rgba(50, 100, 255, 0.1)), url("resources/aboutCover.jpg") no-repeat;
  background: -o-linear-gradient(bottom, rgba(50, 100, 255, 0.3), rgba(50, 100, 255, 0.1)), url("resources/aboutCover.jpg") no-repeat;
  background: linear-gradient(0deg, rgba(50, 100, 255, 0.3), rgba(50, 100, 255, 0.1)), url("resources/aboutCover.jpg") no-repeat; }

#bigPhotography, #bigDesign, #bigCoding {
  /*background: linear-gradient(0deg,rgba(50,100,256,0.3),rgba(50,100,256,0.1)), url("resources/computer.jpg") no-repeat;*/
  background-size: cover;
  z-index: 1; }

/*Modify widths of sections*/
#shortDesign, #shortCode, #bigDesign, #bigCoding, #bigPhotography {
  width: 74.5%; }

#skinnyDesign, #skinnyCode, #skinnyPhotography {
  width: 24.5%; }

/*Modify Heights */
#shortDesign, #shortCode {
  height: 14.25%; }

#bigDesign, #bigCoding {
  z-index: 1;
  height: 84.25%; }

/*Transition Speeds*/
#Design, #skinnyDesign, #shortDesign, #bigDesign, #Coding, #skinnyCode, #shortCode,
#bigCoding, #Photography, #skinnyPhotography, #shortPhotographyTop, #bigPhotography {
  -webkit-transition: ease-out 1s;
  -o-transition: ease-out 1s;
  transition: ease-out 1s; }

.homeButtonText {
  position: absolute;
  bottom: 10px;
  padding: 10px 20px 10px 20px;
  background-color: rgba(125, 125, 125, 0.5);
  word-wrap: break-word;
  max-width: 85%;
  font-size: 1em; }

#photoText {
  right: 10px; }

#codingText, #designText {
  left: 10px; }

@media (max-width: 600px) {
  #photoText {
    right: 0px; }
  .homeButtonText {
    max-width: .75em;
    margin: 0;
    padding: 10px;
    font-size: .75em;
    word-break: break-all;
    text-transform: uppercase;
    text-align: center;
    top: 0px;
    bottom: 0px; }
  #codingText, #designText {
    left: 0px; }
  video {
    visibility: hidden; } }

